// Colors
$green: #79CD47;
$dark-green: #65BA32;
$gray: #54595D;
$light-gray: #888E94;
$black: #000;
$white: #fff;

// Font colors
$text-base: #595959;
$text-dark: #202020;
$text-sub: #555;

// Typography
$font-root-size: 16px;
$font-stack: 'Lato', sans-serif;
$style: normal;

// General
html {
    font-size: $font-root-size;
    box-sizing: border-box;
}
*, *::before, *::after {
    box-sizing: inherit;
}
body {
    color: $text-base;
    font-family: $font-stack;
    font-style: $style;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
a,
a:visited,
a:link {
    color: $green;
    text-decoration: none;
    outline: none;
    transition: all .5s ease;
}
a:hover {
    color: $dark-green;
    text-decoration: none;
    outline: none;
}
ul,
ol {
    clear: both
}
.fl {
    float: left
}

.fr {
    float: right
}

.fn {
    float: none;
    display: inline-block
}

.tal {
    text-align: left
}

.tar {
    text-align: right
}

.tac {
    text-align: center
}

.clr-l {
    clear: left
}

.clr-r {
    clear: right
}

.clr,
.clear {
    clear: both
}
.nodisp {
    display: none
}
h1 {
    font-style: $style;
    font-size: 2.5rem;
    @include bp(md) {
        font-size: 4rem;
    }
}
h2 {
    color: $text-sub;
    font-size: 1.875rem;
    @include bp(md) {
        font-size: 3.5rem;
    }
}
h3 {
    color: $text-sub;
    font-size: 1.25rem;
    @include bp(md) {
        font-size: 2rem;
    }
}
p, li {
    line-height: 2rem;
}
.img-resp {
    max-width: 100%;
    height: auto;
}
