@import url(https://use.fontawesome.com/releases/v5.9.0/css/all.css);
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap");
html {
  font-size: 16px;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  color: #595959;
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a,
a:visited,
a:link {
  color: #79CD47;
  text-decoration: none;
  outline: none;
  transition: all 0.5s ease;
}

a:hover {
  color: #65BA32;
  text-decoration: none;
  outline: none;
}

ul,
ol {
  clear: both;
}

.fl {
  float: left;
}

.fr {
  float: right;
}

.fn {
  float: none;
  display: inline-block;
}

.tal {
  text-align: left;
}

.tar {
  text-align: right;
}

.tac {
  text-align: center;
}

.clr-l {
  clear: left;
}

.clr-r {
  clear: right;
}

.clr,
.clear {
  clear: both;
}

.nodisp {
  display: none;
}

h1 {
  font-style: normal;
  font-size: 2.5rem;
}
@media only screen and (min-width: 768px) {
  h1 {
    font-size: 4rem;
  }
}

h2 {
  color: #555;
  font-size: 1.875rem;
}
@media only screen and (min-width: 768px) {
  h2 {
    font-size: 3.5rem;
  }
}

h3 {
  color: #555;
  font-size: 1.25rem;
}
@media only screen and (min-width: 768px) {
  h3 {
    font-size: 2rem;
  }
}

p, li {
  line-height: 2rem;
}

.img-resp {
  max-width: 100%;
  height: auto;
}

a.btn,
button.btn {
  display: inline-block;
  width: 100%;
  border: none;
  border-radius: 0;
  max-width: 400px;
  color: #fff;
  background: #79CD47;
  text-transform: uppercase;
  padding: 1rem;
  position: relative;
  transition: all 0.3s ease;
}
a.btn::after,
button.btn::after {
  font-size: 0.75rem;
  content: "\f054";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  transition: all 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
  position: relative;
  top: -2px;
  right: -0.5rem;
}
a.btn:hover,
button.btn:hover {
  color: #fff;
  background: #65BA32;
}
a.btn:hover::after,
button.btn:hover::after {
  right: -1.25rem;
}

/* FORM */
form input,
form select,
form textarea {
  font-size: inherit;
  font-family: inherit;
  width: 100%;
  margin: 0 0 10px;
  padding: 15px;
  border: none;
  border-bottom: 1px solid #fff;
  background: none;
  transition: all 0.5s ease;
}
form input:focus,
form select:focus,
form textarea:focus {
  outline: none;
}
form textarea {
  height: 200px;
}
form ::placeholder {
  font-style: normal;
  font-family: inherit;
  font-size: inherit;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
header {
  background: #54595D;
}
@media only screen and (min-width: 768px) {
  header .row {
    display: flex;
    align-items: center;
  }
}
header .top-menu,
header .contact-info {
  text-align: center;
}
@media only screen and (min-width: 768px) {
  header .top-menu,
header .contact-info {
    text-align: right;
  }
}
header .top-menu ul li {
  position: relative;
}
@media only screen and (min-width: 768px) {
  header .top-menu ul li {
    display: inline-block;
  }
  header .top-menu ul li:not(:last-of-type) a::after {
    content: "";
    background: #fff;
    height: 1rem;
    width: 1px;
    display: block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
  }
}
header .top-menu ul li a {
  color: #fff;
  display: inline-block;
  padding: 0.25rem;
}
@media only screen and (min-width: 768px) {
  header .top-menu ul li a {
    padding: 0.25rem 0.5rem;
  }
}
header .contact-info p {
  font-weight: 700;
  color: #fff;
  font-size: 1.15rem;
}
@media only screen and (min-width: 768px) {
  header .contact-info p {
    margin-bottom: 0;
  }
}

#main-nav {
  background: #79CD47;
}
#main-nav .navbar-dark {
  background-color: initial !important;
}
#main-nav .navbar-dark .navbar-toggler {
  color: #fff !important;
}
#main-nav .navbar-dark .nav-link {
  color: #fff !important;
}
@media only screen and (min-width: 1200px) {
  #main-nav .navbar-dark .navbar-nav {
    display: flex;
    width: 100%;
  }
}
@media only screen and (min-width: 1200px) {
  #main-nav .navbar-dark .navbar-nav li {
    text-align: center;
    flex: 1;
  }
}
#main-nav a {
  text-transform: uppercase;
}

#banner {
  background: url(../images/banner.jpg) no-repeat center;
  background-size: cover;
}
@media only screen and (max-width: 575px) {
  #banner .row {
    padding: 0 1rem;
  }
}
#banner .col {
  padding: 8rem 0;
  text-align: center;
}
@media only screen and (min-width: 768px) {
  #banner .col {
    padding: 14rem 0;
  }
}
#banner h1 {
  font-weight: 300;
  color: #fff;
  margin-bottom: 2rem;
}
#banner .btn {
  background: none;
  border: solid 1px #fff;
}
#banner .btn:hover {
  border-color: transparent;
  background: rgba(255, 255, 255, 0.3);
}

#about-section {
  background: url(../images/white-bg.jpg) no-repeat center;
  background-size: cover;
}
#about-section h1 {
  font-weight: 300;
}
#about-section .col {
  text-align: center;
  padding: 6rem 1rem;
  max-width: 850px;
  margin: 0 auto;
}
#about-section .col p {
  font-size: 1.25rem;
}

#category-section .row {
  padding: 6rem 0;
}
#category-section .col-lg-4:not(:last-of-type) {
  margin-bottom: 4rem;
}
@media only screen and (min-width: 992px) {
  #category-section .col-lg-4:not(:last-of-type) {
    margin-bottom: 0;
  }
}
#category-section img {
  margin-bottom: 1.5rem;
}
#category-section p {
  line-height: 1.5;
  font-size: 1.25rem;
}
#category-section .sub-header {
  color: #79CD47;
  font-size: 1.5rem;
}

#nurse-section {
  background: url(../images/nurse-bg.jpg) no-repeat center;
  background-size: cover;
}
#nurse-section .row {
  padding: 6rem 0;
}
#nurse-section h1 {
  font-size: 3.5rem;
  font-weight: 300;
  color: #fff;
  margin-bottom: 1.5rem;
}
#nurse-section p {
  color: #fff;
}
#nurse-section .btn {
  background: none;
  border: solid 1px #fff;
}
#nurse-section .btn:hover {
  border-color: transparent;
  background: rgba(255, 255, 255, 0.3);
}
#nurse-section .badge-wrapper {
  margin-top: 2rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
@media only screen and (max-width: 575px) {
  #nurse-section .badge-wrapper {
    justify-content: center;
  }
}
#nurse-section .badge-wrapper img {
  width: 130px;
  margin-top: 1rem;
  display: inline-block;
  padding-right: 1rem;
}

#contact-section {
  background: #F0F0F0;
}
#contact-section .container > .row {
  padding: 6rem 0;
}
#contact-section label {
  display: none;
}
#contact-section input {
  padding: 1rem;
  border: none;
  border-radius: 0;
}
#contact-section h1 {
  font-weight: 300;
}

footer {
  background: #54595D;
  padding: 3rem 0;
}
footer .col-xl-6 {
  text-align: center;
}
@media only screen and (min-width: 1200px) {
  footer .col-xl-6:first-of-type {
    display: flex;
  }
}
@media only screen and (min-width: 1200px) {
  footer .col-xl-6:first-of-type .contact-info {
    text-align: left;
    padding-left: 1rem;
  }
}
footer .col-xl-6:last-of-type {
  margin-top: 2rem;
}
@media only screen and (min-width: 1200px) {
  footer .col-xl-6:last-of-type {
    margin-top: 0;
    text-align: right;
  }
}
footer p {
  color: #fff;
}
footer hr {
  margin: 2rem 0;
  background-color: rgba(255, 255, 255, 0.6);
}
footer .social a {
  color: #fff;
  display: inline-block;
  margin: 0 0.5rem;
  font-size: 1.25rem;
}
footer .col {
  text-align: center;
  margin-bottom: 2rem;
}
@media only screen and (min-width: 1200px) {
  footer .col {
    text-align: left;
  }
}
footer .footer-menu {
  padding-left: 0;
}
footer .footer-menu li {
  display: inline-block;
}
footer .footer-menu li a {
  color: #fff;
  padding: 0.5rem;
}
footer .follow-wrapper a {
  padding: 0 0.25rem;
}
footer .follow-wrapper img {
  max-height: 30px;
  border-radius: 4px;
}